/* stars region */
.stars {
  display: flex;

  &__star {
    height: 16px;
    width: 16px;
    background-image: url(../images/star.svg);
    background-repeat: no-repeat;
    background-position: center;
    margin-right: 4px;
  }

  @for $i from 1 through 5 {
    &--#{$i} :nth-child(-n + #{$i}) {
      background-image: url(../images/star-active.svg);
    }
  }

  & :last-child {
    margin-right: 0;
  }
}
